import React from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext'; // Import the useAuth hook
import 'bootstrap/dist/css/bootstrap.min.css';
import './NavBar.css'; // Custom CSS
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faBox, faInfoCircle, faEnvelope, faShoppingCart, faSignInAlt, faUserPlus, faSignOutAlt } from '@fortawesome/free-solid-svg-icons';

export default function NavBar() {
  const { user, logout, isAuthenticated } = useAuth(); // Get user and functions from context
  const navigate = useNavigate();

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light shadow-sm sticky-top animate-navbar">
      <div className="container">
        <NavLink className="navbar-brand d-flex align-items-center" to="/">
          <span className="navbar-logo me-2"></span> Dora-Cart
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarTogglerDemo01"
          aria-controls="navbarTogglerDemo01"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <NavLink className="nav-link" aria-current="page" to="/">
                <FontAwesomeIcon icon={faHome} className="me-1" /> Home
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/products">
                <FontAwesomeIcon icon={faBox} className="me-1" /> Products
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/about">
                <FontAwesomeIcon icon={faInfoCircle} className="me-1" /> About
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/contact">
                <FontAwesomeIcon icon={faEnvelope} className="me-1" /> Contact
              </NavLink>
            </li>
          </ul>
          <div className="d-flex align-items-center">
            {isAuthenticated ? (
              <>
                <span className="navbar-text me-3">Hello, {user.username}</span>
                <NavLink to="/profile" className="btn btn-outline-primary me-2">
                  <FontAwesomeIcon icon={faUserPlus} className="me-1" /> Profile
                </NavLink>
                <button onClick={handleLogout} className="btn btn-outline-primary me-2">
                  <FontAwesomeIcon icon={faSignOutAlt} className="me-1" /> Logout
                </button>
              </>
            ) : (
              <>
                <NavLink to="/login" className="btn btn-outline-primary me-2">
                  <FontAwesomeIcon icon={faSignInAlt} className="me-1" /> Login
                </NavLink>
                <NavLink to="/register" className="btn btn-outline-primary me-2">
                  <FontAwesomeIcon icon={faUserPlus} className="me-1" /> Register
                </NavLink>
              </>
            )}
            <NavLink to="/cart" className="btn btn-outline-primary position-relative">
              <FontAwesomeIcon icon={faShoppingCart} className="me-1" /> Cart
              <span className="cart-count">{/* Replace with actual cart item count */}</span>
            </NavLink>
          </div>
        </div>
      </div>
    </nav>
  );
}
