import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Container, Form, Button, Row, Col } from 'react-bootstrap';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import './LoginPage.css';
import { useAuth } from '../../context/AuthContext'; // Import the useAuth hook

export default function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const { login } = useAuth(); // Get the login function from context

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('http://localhost:5000/login', { email, password });

      if (response.data.username) {
        login({ username: response.data.username }); // Use the login function from context
        setError('');
        navigate('/home'); // Redirect to the Home component path
      } else {
        setError(response.data.message || 'Login failed'); // Display error message
      }
    } catch (err) {
      console.error('Login error:', err.response ? err.response.data : err.message);
      setError(err.response ? err.response.data.message : 'An unexpected error occurred');
    }
  };

  return (
    <Container className="login-container mt-5">
      <Row className="justify-content-center">
        <Col md={8} lg={6}>
          <div className="form-header text-center mb-4 p-4">
            <h1 className="display-4 mb-3">Login</h1>
            <p className="lead mb-4">Access your account using your credentials.</p>
          </div>
          <Form className="login-form" noValidate onSubmit={handleSubmit}>
            <Form.Group as={Row} className="mb-3" controlId="formEmail">
              <Form.Label column sm={3}>Email</Form.Label>
              <Col sm={9}>
                <Form.Control
                  type="email"
                  placeholder="you@example.com"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                  className="form-control-custom"
                />
              </Col>
            </Form.Group>

            <Form.Group as={Row} className="mb-4" controlId="formPassword">
              <Form.Label column sm={3}>Password</Form.Label>
              <Col sm={9}>
                <Form.Control
                  type="password"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                  className="form-control-custom"
                />
              </Col>
            </Form.Group>

            {error && <p className="text-danger">{error}</p>}

            <Button variant="primary" type="submit" className="w-100 btn-custom">
              Login
            </Button>
          </Form>
        </Col>
      </Row>
    </Container>
  );
}
