import React from "react";
import "./App.css";
import NavBar from "./components/NavBar";
import Home from "./components/Home";
import Products from "./components/Products";
import Product from "./components/Product";
import Cart from "./components/Cart";
import Login from "./components/Login";
import Register from "./components/Register";
import Contact from "./components/Contact";
import About from "./components/About";
import Profile from "./components/Profile"; // Import Profile component
import { Routes, Route, Navigate } from "react-router-dom";
import { useAuth } from "./context/AuthContext"; // Import the useAuth hook

// App component
function App() {
  const { user } = useAuth(); // Use the useAuth hook

  return (
    <>
      <NavBar />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/products" element={<Products />} />
        <Route path="/products/:id" element={<Product />} />
        <Route path="/cart" element={<Cart />} />
        <Route
          path="/login"
          element={user ? <Navigate to="/" replace /> : <Login />} // Redirect to home if already authenticated
        />
        <Route
          path="/register"
          element={user ? <Navigate to="/" replace /> : <Register />} // Redirect to home if already authenticated
        />
        <Route path="/contact" element={<Contact />} />
        <Route path="/about" element={<About />} />
        <Route
          path="/home"
          element={user ? <Home /> : <Navigate to="/login" replace />} // Redirect to login if not authenticated
        />
        <Route
          path="/profile"
          element={user ? <Profile /> : <Navigate to="/login" replace />} // Profile route, redirect to login if not authenticated
        />
      </Routes>
    </>
  );
}

export default App;
