import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { updateUser } from '../redux/actions'; // Import the action to update user info
import './Profile.css'; // Import your CSS file for styling

export default function Profile() {
  const user = useSelector((state) => state.user); // Get user info from Redux state
  const dispatch = useDispatch();
  const [editMode, setEditMode] = useState(false); // Edit mode toggle
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
  });

  // Fetch user details from the store
  useEffect(() => {
    if (user) {
      setFormData({
        name: user.name || '',
        email: user.email || '',
        phone: user.phone || '',
      });
    }
  }, [user]);

  // Handle form input changes
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  // Save updated user details
  const handleSave = () => {
    dispatch(updateUser(formData)); // Dispatch the action to update user data in Redux and DB
    setEditMode(false); // Exit edit mode
  };

  // Cancel edits and reset form data to current user info
  const handleCancel = () => {
    setEditMode(false);
    setFormData({
      name: user.name || '',
      email: user.email || '',
      phone: user.phone || '',
    });
  };

  // Show a loading indicator until the user data is available
  if (!user) return <p>Loading...</p>;

  return (
    <div className="profileContainer">
      <h1>Profile</h1>
      {editMode ? (
        <div className="profileForm">
          <div className="formGroup">
            <label htmlFor="name">Name:</label>
            <input
              type="text"
              id="name"
              name="name"
              value={formData.name}
              onChange={handleChange}
            />
          </div>
          <div className="formGroup">
            <label htmlFor="email">Email:</label>
            <input
              type="email"
              id="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
            />
          </div>
          <div className="formGroup">
            <label htmlFor="phone">Phone:</label>
            <input
              type="text"
              id="phone"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
            />
          </div>
          <button onClick={handleSave} className="btn btn-primary">Save</button>
          <button onClick={handleCancel} className="btn btn-secondary">Cancel</button>
        </div>
      ) : (
        <div className="profileDetails">
          <p><strong>Name:</strong> {user.name}</p>
          <p><strong>Email:</strong> {user.email}</p>
          <p><strong>Phone:</strong> {user.phone}</p>
          <button onClick={() => setEditMode(true)} className="btn btn-primary">Edit Profile</button>
        </div>
      )}
    </div>
  );
}
