// src/components/Cart.jsx

import React, { useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import CartProduct from './cart/CartProduct'; // Ensure this path is correct
import AddressForm from './cart/AddressForm'; // Ensure this path is correct
import './Cart.css'; // Ensure this path is correct

export default function Cart() {
  const [showAddressForm, setShowAddressForm] = useState(false);
  const cartItems = useSelector((state) => state.cart || []);

  const handlePlaceOrder = () => {
    setShowAddressForm(true); // Show address form when placing an order
  };

  return (
    <div className="cartContainer container">
      <h1 className="text-center mb-4">Your Cart</h1>
      <div className="row">
        <div className="col-md-8">
          {cartItems.length > 0 ? (
            cartItems.map((item) => (
              <CartProduct key={item.id} cartProduct={item} />
            ))
          ) : (
            <div className="emptyCart text-center">
              <h2>Your cart is empty!</h2>
              <Link to="/products" className="btn btn-primary">
                Start Shopping
              </Link>
            </div>
          )}
        </div>
        <div className="col-md-4 d-flex flex-column align-items-center">
          {cartItems.length > 0 && (
            <button onClick={handlePlaceOrder} className="btn btn-success btn-lg w-100 mt-3">
              <i className="fas fa-credit-card"></i> Place Order
            </button>
          )}
        </div>
      </div>

      {showAddressForm && <AddressForm onClose={() => setShowAddressForm(false)} />}
    </div>
  );
}
