import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import { addCart } from "../redux/actions";
import { useParams, NavLink } from "react-router-dom";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import "./Product.css"; // Import the CSS file for styling

export default function Product() {
  const { id } = useParams();
  const [product, setProduct] = useState(null); // Initialize to null
  const [loading, setLoading] = useState(true);
  const dispatch = useDispatch();

  const addProduct = (product) => {
    dispatch(addCart(product));
  };

  useEffect(() => {
    let isMounted = true; // Track whether component is mounted

    const getProduct = async () => {
      try {
        const response = await fetch(`https://fakestoreapi.com/products/${id}`);
        
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const contentType = response.headers.get("content-type");
        if (!contentType || !contentType.includes("application/json")) {
          throw new Error("Received non-JSON response");
        }

        const productData = await response.json();
        
        if (isMounted) { // Only update state if component is still mounted
          setProduct(productData);
          setLoading(false);
        }
      } catch (error) {
        console.error("Error fetching product:", error);
      }
    };

    getProduct();

    return () => {
      isMounted = false; // Cleanup to avoid updating state when unmounted
    };
  }, [id]); // Ensure that `id` is included in the dependency array

  const Loading = () => (
    <div className="row">
      <div className="col-md-6">
        <Skeleton height={250} width={250} />
      </div>
      <div className="col-md-6">
        <Skeleton height={50} width={300} />
        <Skeleton height={75} />
        <Skeleton height={25} width={150} />
        <Skeleton height={50} />
        <Skeleton height={150} />
        <Skeleton height={50} width={100} />
      </div>
    </div>
  );

  const ShowProduct = () => (
    <div className="row">
      <div className="col-md-6 d-flex justify-content-center">
        <img
          src={product.image}
          alt={product.title}
          className="product-image animate__animated animate__fadeIn"
        />
      </div>
      <div className="col-md-6">
        <h4 className="text-uppercase text-black-50 category">
          {product.category}
        </h4>
        <h1 className="display-5 title">{product.title}</h1>
        <p className="lead rating">
          Rating: {product.rating?.rate || 'N/A'}
          <i className="fa fa-star"></i>
        </p>
        <h3 className="display-6 price"> {product.price}</h3>
        <p className="lead description">{product.description}</p>
        <div className="button-group">
          <button
            className="btn btn-outline-dark px-4 py-2 add-to-cart animate__animated animate__pulse"
            onClick={() => addProduct(product)}
          >
            <i className="fa fa-cart-plus"></i> Add to Cart
          </button>
          <NavLink
            to="/cart"
            className="btn btn-dark ms-2 px-3 py-2 go-to-cart animate__animated animate__pulse"
          >
            <i className="fa fa-shopping-cart"></i> Go to Cart
          </NavLink>
        </div>
      </div>
    </div>
  );

  return (
    <div className="container py-5">
      {loading ? <Loading /> : product ? <ShowProduct /> : <p>Product not found</p>}
    </div>
  );
}
