// src/components/cart/CartProduct.jsx

import React from "react";
import { useDispatch } from "react-redux";
import { addCart, delCart, rmvCart } from "../../redux/actions";
import './CartProduct.css';

export default function CartProduct({ cartProduct }) {
  const dispatch = useDispatch();

  const handleRemove = () => {
    dispatch(rmvCart(cartProduct));
  };

  const handleReduce = () => {
    if (cartProduct.qty === 0) return;
    dispatch(delCart(cartProduct));
  };

  const handleAdd = () => {
    dispatch(addCart(cartProduct));
  };

  return (
    <div className="cartProductContainer row mb-4 p-3 border rounded shadow-sm animated fadeIn">
      <div className="col-4">
        <img
          src={cartProduct.image}
          className="img-fluid"
          alt={cartProduct.title}
        />
      </div>
      <div className="col-8">
        <h4>{cartProduct.title}</h4>
        <div className="quantityControl">
          <button className="btn btn-light" onClick={handleReduce}>
            <i className="fas fa-minus"></i>
          </button>
          <span className="quantity">{cartProduct.qty}</span>
          <button className="btn btn-light" onClick={handleAdd}>
            <i className="fas fa-plus"></i>
          </button>
        </div>
        <p>Price:  {cartProduct.price}</p>
        <button className="btn btn-danger" onClick={handleRemove}>
          <i className="fas fa-trash"></i> Remove
        </button>
      </div>
    </div>
  );
}
