// redux/actions/index.js

export const ADD_CART = 'ADD_CART';
export const DEL_CART = 'DEL_CART';
export const RMV_CART = 'RMV_CART';
export const SET_USER = 'SET_USER';
export const LOGOUT_USER = 'LOGOUT_USER';
export const UPDATE_USER = 'UPDATE_USER'; // Add UPDATE_USER action type

export const addCart = (product) => ({
  type: ADD_CART,
  payload: product,
});

export const delCart = (product) => ({
  type: DEL_CART,
  payload: product,
});

export const rmvCart = (product) => ({
  type: RMV_CART,
  payload: product,
});

export const setUser = (user) => ({
  type: SET_USER,
  payload: user,
});

export const logoutUser = () => ({
  type: LOGOUT_USER,
});

export const updateUser = (user) => ({
  type: UPDATE_USER,
  payload: user, // Payload will be the updated user info
});
