// redux/reducers/userReducer.js

import { SET_USER, LOGOUT_USER, UPDATE_USER } from '../actions/index';

const initialState = {
  user: null,
};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER:
      return {
        ...state,
        user: action.payload,
      };
    case LOGOUT_USER:
      return {
        ...state,
        user: null,
      };
    case UPDATE_USER: // Handle UPDATE_USER action
      return {
        ...state,
        user: {
          ...state.user,
          ...action.payload, // Merge the updated user data
        },
      };
    default:
      return state;
  }
};

export default userReducer;
